@include color-mode(dark) {
  .highlight > .chroma {
    border: 1px solid color-mix(in srgb, var(--sl-color-gray-5), transparent 25%);
  }

  /*
  .chroma .hl {
    background-color: #ffffff17;
  }
  */

  .chroma .hl {
    border-inline-start: 0.15rem solid #ffffff40;
    margin-left: -1rem;
    margin-right: -1rem;
    padding-left: 1rem;
    padding-right: 1rem;
  
    .ln {
      margin-left: -0.15rem;
    }
  }
}
