/*
 * Source: https://medium.com/creative-technology-concepts-code/responsive-mobile-dropdown-navigation-using-css-only-7218e4498a99
*/

/* Style the menu icon for the dropdown */

.navbar .menu-icon {
  cursor: pointer;

  /* display: inline-block; */

  /* float: right; */
  padding: 1.125rem 0.625rem;
  margin: 0 0 0 -0.625rem;

  /* position: relative; */
  user-select: none;
}

.navbar .menu-icon .navicon {
  background: $navbar-light-color;
  display: block;
  height: 2px;
  position: relative;
  transition: background 0.2s ease-out;
  width: 18px;
}

.navbar .menu-icon .navicon::before,
.navbar .menu-icon .navicon::after {
  background: $navbar-light-color;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  width: 100%;
}

.navbar .menu-icon .navicon::before {
  top: 5px;
}

.navbar .menu-icon .navicon::after {
  top: -5px;
}

/* Add the icon and menu animations when the checkbox is clicked */

.navbar .menu-btn {
  display: none;
}

.navbar .menu-btn:checked ~ .navbar-collapse {
  display: block;
  max-height: 100vh;
}

.navbar .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent;
}

.navbar .menu-btn:checked ~ .menu-icon .navicon::before {
  transform: rotate(-45deg);
}

.navbar .menu-btn:checked ~ .menu-icon .navicon::after {
  transform: rotate(45deg);
}

.navbar .menu-btn:checked ~ .menu-icon:not(.steps) .navicon::before,
.navbar .menu-btn:checked ~ .menu-icon:not(.steps) .navicon::after {
  top: 0;
}

.btn-menu {
  margin-left: 1rem;
  border: transparent;
}

.btn-doks-light {
  border: transparent;
}

.btn-menu,
.doks-sidebar-toggle {
  padding-right: 0.25rem;
  padding-left: 0.25rem;
  margin-right: -0.5rem;
}

.btn-menu:hover,
.btn-doks-light:hover,
.doks-sidebar-toggle:hover {
  background: transparent;
  border: transparent;
}

.btn-menu:focus,
.btn-doks-light:focus,
.doks-sidebar-toggle:focus,
.doks-mode-toggle:focus {
  outline: 0;
  border: transparent;
}

.doks-sidebar-toggle .doks-collapse,
.doks-toc-toggle .doks-collapse {
  display: none;
}

.doks-sidebar-toggle:not(.collapsed) .doks-expand,
.doks-toc-toggle:not(.collapsed) .doks-expand {
  display: none;
}

.doks-sidebar-toggle:not(.collapsed) .doks-collapse,
.doks-toc-toggle:not(.collapsed) .doks-collapse {
  display: inline-block;
}

.navbar-light .navbar-brand,
.navbar-light .navbar-brand:hover,
.navbar-light .navbar-brand:active {
  color: $body-color;
}

.navbar-light .navbar-nav .active .nav-link {
  color: $primary;
}

.dropdown-divider {
  border-top: 1px dashed $gray-200;
}

.dropdown-item:hover {
  background: $gray-100;
}

.dropdown-item:active {
  color: inherit;
}

.social-link {
  padding-right: 0.375rem;
  padding-left: 0.375rem;
}

@include media-breakpoint-down(lg) {
  #buttonColorMode  {
    margin: 0.5rem 0;
  }

  #socialMenu {
    margin: 0.5rem 0 0.5rem -0.25rem;
  }

  .navbar-nav {
    margin-top: 1rem;
  }

  .dropdown-menu {
    box-shadow: none !important;
    background: transparent !important;
    border-radius: 0 !important;
    padding: 0;
    margin-bottom: 0.25rem;
  }

  .dropdown-item {
    padding: 0.375rem 1rem 0.375rem 0;
  }

  .nav-item .nav-link {
    font-weight: $font-weight-normal;
    font-size: $font-size-md;
  }

  .btn-dropdown {
    font-weight: $font-weight-normal;
    font-size: $font-size-md;
  }
}

