.modal-backdrop,
.offcanvas-backdrop {
  visibility: hidden;
  background: hsla(224deg, 10%, 10%, 0.5);
  opacity: 0
}

@include color-mode(dark) {
  .modal-backdrop,
  .offcanvas-backdrop {
    visibility: hidden;
    background: hsla(224deg, 10%, 10%, 0.5);
    opacity: 0
  }
}

.modal-backdrop.show,
.offcanvas-backdrop.show {
  visibility: visible;
  opacity: 1;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px)
}

.showing,
.hiding {
  -webkit-transition: none;
  transition: none;
  display: none;
}

.offcanvas-top.h-auto {
  bottom: initial;
}

.navbar > .container,
.navbar > .container-fluid,
.navbar > .container-sm,
.navbar > .container-md,
.navbar > .container-lg,
.navbar > .container-xl,
.navbar > .container-xxl {
  padding-right: 0.75rem;
}
