/* Show the sun icon if the bs theme is dark */
html[data-bs-theme="dark"] {
  .icon-tabler-sun {
    display: block;
  }

  .icon-tabler-moon {
    display: none;
  }
}

/* Show the moon icon if the bs theme is light */
html[data-bs-theme="light"] {
  .icon-tabler-sun {
    display: none;
  }

  .icon-tabler-moon {
    display: block;
  }
}

