.logo-netlify-large-fullcolor-darkmode {
  display: none;
}

@include color-mode(dark) {
  .logo-netlify-large-fullcolor-lightmode {
    display: none;
  }

  .logo-netlify-large-fullcolor-darkmode {
    display: block;
  }
}

.svg-lightmode {
  display: block;
}

.svg-darkmode {
  display: none;
}

.svg-monochrome path {
  fill: $db-bluishCyan-100;
}

@include color-mode(dark) {
  .svg-lightmode {
    display: none;
  }

  .svg-darkmode {
    display: block;
  }

  .netlify-logo path,
  .netlify-monogram path {
    fill: $white;
  }

  .svg-monochrome path {
    fill: var(--sl-color-gray-1);
  }
}

hr {
  border-color: $db-gray-700;
}

@include color-mode(dark) {
  hr {
    border-color: var(--sl-color-gray-3);
  }
}

// Fix for overflowing code block
.container-fw {
  min-width: 0;
}

// Link cards
.card-nav {
  column-gap: 1rem;
}

.card-nav .card {
  margin: 0.5rem 0;
}

.card-nav .card:hover {
  border: 1px solid $db-gray-600;
  background-color: var(--sl-color-gray-7);
}

@include color-mode(dark) {
  .card-nav .card {
    border: 1px solid hsl(224deg, 10%, 23%);
  }

  .card-nav .card:hover {
    border: 1px solid hsl(224deg, 6%, 56%);
    background-color: var(--sl-color-gray-6);
  }
}
