.page-nav .card {
  .icon-tabler-arrow-left {
    margin-right: 0.75rem;
  }

  .icon-tabler-arrow-right {
    margin-left: 0.75rem;
  }
}

.page-nav .card:hover {
  border: 1px solid $db-gray-600;
}

@include color-mode(dark) {
  .page-nav .card {
    border: 1px solid hsl(224deg, 10%, 23%);
  }

  .page-nav .card:hover {
    border: 1px solid hsl(224deg, 6%, 56%);
  }
}

.container-fw {
  max-width: 1200px;

  .docs-toc {
    margin-left: 3rem;
  }
}